@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  outline: none;
}
.App{
    width: 100%;
    display: flex;
    justify-content: center;
}

.AppContents{
    width: 100%;
    max-width: 1280px;
}

.author{
    width: 100%;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.author h1{
    text-transform: uppercase;
    letter-spacing: 2px;
    color: black;
    font-weight: 900;
    font-size: 4rem;
    margin: 0;
    margin-bottom: 150px;
    -webkit-text-stroke: red 1px;
}

.author a{
    text-decoration: none;
}
.mainHero{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.mainHero img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainHero::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right,black, transparent 50% ,black),
                linear-gradient(to top,black, transparent 20%, transparent);
    background-color: rgba(0, 0, 0, 0.479);
    
}

.mainHeroTitles{
    position: absolute;
    color: #fff;
    font-weight: 300;
    left: 5%;
    bottom: 15%;
    display: flex;
    flex-direction: column;
}

.mainHeroTitles *{
    margin: 0;
}

.mainHeroTitles h1{
    font-weight: 100;
    font-size: 3rem;
}

.mainHeroTitles p{
    font-size: 1.4rem;
}

.mainHeroLogo{
    position: absolute;
    bottom: 15%;
    right: 5%;
    width: 15%;
}

.mainHeroLogo img{
    -webkit-filter: drop-shadow(1px 1px 0 #fff) 
            drop-shadow(-1px -1px 0 #fff);
            filter: drop-shadow(1px 1px 0 #fff) 
            drop-shadow(-1px -1px 0 #fff);
}


.mainNavbar{
    width: 100%;
    height: 100px;
    position: absolute;
    top: 0;
    display: flex;
    justify-content: space-between;
    padding: 0 5%;
}

.mainNavbarLogo{
    width: 100px;
    height: 80px;
    border-bottom: solid 1px red;
}

.mainNavbarLogo img{
    width: 100%;
    cursor: pointer;
    transform: translateY(12px);
}

.mainNavbarButtons{
    height: 80px;
    border-bottom: solid red 1px;
    display: flex;
    position: relative;
    align-items: flex-end;
    /* background-color: ivory; */
}

.mainNavbarButtons button{
    font-weight: 300;
    background: transparent;
    color: #fff;
    border-style: none;
    height: 35px;
    width: 120px;
    margin: 10px;
    border: solid 1px white;
    cursor: pointer;
    transition: 0.2s;
    text-transform: uppercase;
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainNavbarButtons button:hover{
    border-color: red;
    color: red;
    font-weight: 800;
}
.mainPerfomance{
    margin-top: 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 200px;
}

.mainPerfomanceTitle{
    width: 75%;
    /* height: 50px; */
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-left: 1px solid red;
    padding: 20px;
    text-align: center;
}

.mainPerfomanceTitle * {
    margin: 0;
    font-weight: 100;
}

.mainPerfomanceTitle h2{
    font-size: 5rem;
}

.mainPerfomanceTitle p{
    font-size: 2rem;
}

.mainPerfomanceBoxes{
    width: 100%;
    height: 100px;
    margin-top: 150px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    padding: 0 5%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mainPerfomanceBox{
    width: 100%;
    height: 100%;
    border-bottom: solid red 1px;
    display: flex;
}

.mainPerfomanceBoxImage{
    width: 150px;
    height: 100%;
    padding: 15px;
}

.mainPerfomanceBoxImage img{
    width: 100%;
    height: 100%;
    fill:lawngreen
}

.mainPerfomanceBoxDesc{
    width: 100%;
    height: 100%;
    display: flex;
    color: #fff;
    flex-direction: column;
    justify-content: center;
}

.mainPerfomanceBoxDesc *{
    margin: 0;
    font-weight: 300;
}

.mainPerfomanceBoxDesc h3{
    margin-bottom: 2px;
    font-weight: 400;
}

.mainSpecs{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 75px;
}

.mainSpecsTitle{
    width: 100%;
    color: #fff;
    padding: 5%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: hidden;
}

.mainSpecsTitle h2{
    margin: 0;
    font-size: 5rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mainSpecsTitle::before{
    content: "FEATURES";
    position: absolute;
    color: transparent;
    -webkit-text-stroke: 1px #fff;
    font-weight: 800;
    font-size: 13rem;
}

.mainSpecsContent{
    display: flex;
    width: 100%;
    height: 700px;
    margin-top: 150px;
}

.mainSpecsContentText{
    height: 100%;
    flex: .5 1;
    background-color: black;
    z-index: 1;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    /* justify-content: center; */
    align-items: center;
    /* padding: 5%; */
}

.mainSpecsContentImage{
    height: 100%;
    flex: .5 1;
    position: relative;
    overflow: hidden;
}

.mainSpecsContentImage::before{
    content: "";
    position: absolute;
    /* background: linear-gradient(to right,black, transparent 50% ,transparent); */
    z-index: 1000;
    width: 100%;
    height: 100%;
}

.mainSpecsContentImage img{
    position: absolute;
    height: 100%;
    left: -70px;
}

.mainSpecsContentTextItem{
    width: 30%;
    color: #fff;
}


.mainSpecsContentTextItem h4{
    font-weight: 500;
    padding-bottom: 5px;
    border-bottom: solid 1px red;
}

.mainSpecsContentTextItem p{
    font-weight: 300;
}

.mainSpecs hr{
    margin-top: 75px;
    border: solid 1px red;
    width: 100%;
}
.mainGrid{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    margin-bottom: 250px;
}

.mainGridItem{
    padding-top: 100%;
    position: relative;
}

.mainGridItem img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
}

.mainGridItem:first-of-type{
    position: relative;
}

.mainGridAllSubitem{
    position: absolute;
    display: grid;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    top: 0;
}

.mainGridSubItem{
    position: relative;
    padding-top: 100%;
    transition: .2s;
}

.subItem{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}

.card{
    background-color: aliceblue;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: scale linear .5s, box-shadow linear .5s;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-image{
    position: absolute;
    width: 100%;
    height: 100%;
}

.card-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card-image::before{
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, black, transparent);
    /* background-color: rgba(0, 0, 0, 0.74); */
    z-index: 1000;
}

.card-title{
    position: absolute;
    z-index: 1000;
    color: white;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-size: 1.5rem;
}

.card-title::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 5rem;
    color: transparent;
    z-index: 1000;
    -webkit-text-stroke: 2px rgba(255, 255, 255, 0.267);
    transform: translateY(100%);
}

#first::before {
    content: "R8";
}

#second::before {
    content: "Audi";
}
.mainPerfBottom{
    width: 100%;
    margin-bottom: 150px;
    display: flex;
    flex-direction: column;
}

.mainPerfBottomStats{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    color: #fff;
}

.mainPerfBottomStatsItem{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.mainPerfBottomStatsItem *{
    position: absolute;
}

.mainPerfBottomStatsItem h3{
    font-size: 5rem;
    font-weight: 800;
    text-transform: uppercase;
    bottom: -100px;
}

.mainPerfBottomStatsItem p{
    font-size: 6rem;
    font-weight: 900;
    color: transparent;
    -webkit-text-stroke: 1px #fff;
}

.mainPerfBottomStatsItem span{
    bottom: -60px;
}

.mainPerfBottom hr{
    border: solid 1px red;
    width: 100%;
}

.mainPerfBottomVideo{
    width: 100%;
    margin-top: 150px;
}

.mainPerfBottomVideo video{
    width: 100%;
}
